import React, { Fragment } from 'react';
import TextField from "@mui/material/TextField";
import MFormControl from '@mui/material/FormControl';
import InputLabel from '@mui/material/InputLabel';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import FormLabel from '@mui/material/FormLabel';
import FormControlLabel from '@mui/material/FormControlLabel';
import RadioGroup from '@mui/material/RadioGroup';
import Radio from '@mui/material/Radio';
import {DatePicker} from '@mui/x-date-pickers/DatePicker';
import BasicFormControl from './base-form-control';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { IRenderField } from '../../constants/common-interface';
import enLocale from 'date-fns/locale/en-US';
import { Button, Checkbox, FormGroup, FormHelperText } from '@mui/material';
import { MandatoryLabel } from './common';


export default class MUIFormControl extends BasicFormControl {
    variant: string;
    size: any;
    constructor(props: IRenderField){
        super(props);
    }
    render(): JSX.Element {
        const muiVariant = this.context.getThemeProp('mui','variant');
        const muiSize = this.context.getThemeProp('mui','size');
        this.variant = muiVariant ? muiVariant : 'standard';
        this.size = muiSize ? muiSize : undefined;
        return super.render();
    }
    getVariant() {
        return this.field.meta?.mui?.variant ? this.field.meta.mui.variant : this.variant;
        
    }
    getDisplayLabel() {
        return this.props.form.validation?.required ? this.props.form.displayName + ' *' : this.props.form.displayName;
    }
    date() {
        const label = this.getDisplayLabel();
        let value = this.props.form?.value? new Date(this.props.form?.value) : null;
        const variant = this.getVariant();
        const min = this.props.form.validation?.min? new Date(this.props.form.validation.min) : undefined;
        const max = this.props.form.validation?.max? new Date(this.props.form.validation.max) : undefined;
        return (
            <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={enLocale}>
                <DatePicker
                    disabled={this.props.form.isDisabled}
                    readOnly={this.props.form.isReadonly}
                    label={label}
                    value={value}
                    inputFormat="dd/MM/yyyy"
                    minDate={min}
                    maxDate={max}
                    onChange={(val: Date) => {
                        if (val) {
                            const day = val.getDate();
                            const month = val.getMonth();
                            const year = val.getFullYear();
                            this.handleChange(null, `${year}-${month}-${day}`);
                        } else {
                            this.handleChange(null, '');
                        }
                    }}
                    onClose={this.handleValidation.bind(this)}
                    renderInput={(params) => <TextField {...params} variant={variant}
                        helperText={this.state.error.errorMsg||undefined} 
                        onBlur={this.handleValidation.bind(this)}
                        size={this.size}
                        error={this.state.error?.hasError?true:undefined} fullWidth/>}
                />
            </LocalizationProvider>
        ) 
    }
    input(type: string) {
        const label = this.getDisplayLabel();
        const variant = this.getVariant();
        return (
                <TextField type={type} label={label} variant={variant} fullWidth
                    disabled={this.props.form.isDisabled}
                    InputProps={{
                        readOnly: this.props.form.isReadonly
                    }}
                    value={this.props.form?.value}
                    error={this.state.error?.hasError?true:undefined}
                    helperText={this.state.error.errorMsg||undefined}
                    onChange={this.handleChange.bind(this)}
                    onBlur={this.handleValidation.bind(this)}
                    size={this.size}
                />
        )
    }
    text() {
        return this.input('text');
    }
    password() {
        return this.input('password');
    }
    email() {
        return this.input('email');
    }
    number() {
        return this.input('number');
    }
    radio() {
        const meta = this.props.form;
        return (
            <MFormControl size={this.size} fullWidth>
                <FormLabel id="meta-radio-buttons-group-label">{meta.displayName}{this.validation.required&&<MandatoryLabel/>}</FormLabel>
                <RadioGroup value={this.props.form?.value} onChange={this.handleChange.bind(this)}>
                    {
                        meta.options && meta.options.map(option=>{
                            return (<FormControlLabel key={option.value} value={option.value} control={<Radio />} label={option.label} />)
                        })
                    }
                </RadioGroup>
            </MFormControl>
        )
    }
    checkbox(): JSX.Element {
        const meta = this.props.form;
        return (
            <FormGroup>
                {
                    meta.options && meta.options.map((option: {label: string, value: any}) => {
                        return (
                            <FormControlLabel key={option.value} value={option.value} control={<Checkbox checked={option.value === this.props.form.value} 
                                onChange={(e) => {
                                    const checked = e.target.checked;
                                    this.handleChange(e, checked ? option.value : '');
                                }}
                                disabled={this.props.form.isDisabled}
                            />} label={option.label}
                            />
                        )
                    })
                }
            </FormGroup>
        )
    }
    select() {
        const meta = this.props.form;
        const options = this.props.form.options || [];
        const variant = this.getVariant();
        const label = this.getDisplayLabel();
        return (
            <MFormControl size={this.size} fullWidth error={this.state.error.hasError ? true: undefined} variant={variant}>
                <InputLabel id="meta-select-label">{label}</InputLabel>
                <Select
                    label={meta.displayName}
                    value={this.props.form?.value}
                    disabled={this.props.form.isDisabled}
                    onChange={(e) => this.handleChange(e)}
                    onBlur={this.handleValidation.bind(this)}
                >
                    {
                        options && options.map((option: {label: string, value: string})=>{
                            return (<MenuItem key={option.value} value={option.value}>{option.label}</MenuItem>)
                        })
                    }
                </Select>
                {this.showValidation()}
            </MFormControl>
        )
    }
    button() {
        return (
            <Button variant="contained">Contained</Button>
        )
    }
    showValidation() {
        return (
            <FormHelperText>
                {this.state.error.errorMsg}
            </FormHelperText>
        )
    }
}