import { Button } from '@mui/material';
import { areDayPropsEqual } from '@mui/x-date-pickers/internals';
import React, { SyntheticEvent } from 'react';
import { IField, IMeta } from '../../constants/model-interfaces';
import FormUtils from '../../utils/FormUtil';

export default function(props: IProps) {
    const handleClick = (e: SyntheticEvent, button: IField) => {
        e.preventDefault();
        switch (button?.meta?.events?.onClick) {
            case 'submit':
                props.onSubmit(e, button);
                break;
            case 'reset':
                props.onReset(e, button);
                break;
        }
    }
    return (
        <div className="row">
            {
                props.buttons && props.buttons.map(button => {
                    const className = FormUtils.getCssClassName(button.meta?.displayProps);
                    const subprops = {className, button, theme: props.theme, form: props.form, handleClick}
                    return (
                        <FormButton {...subprops} key={button.name}/>
                    )
                })
            }
        </div>
    )
}

function FormButton({ button, theme, form, className, handleClick }: { button: IField, theme: string, form: any, className: string, handleClick: Function }) {
    
    const btnClassName = button?.meta?.className;
    const colClassName = className ? className : 'col';
    switch (theme) {
        case 'mui':
            return (
                <div className={colClassName}>
                    <Button className={btnClassName}
                        onClick={(e) => handleClick(e, button)}
                    >{button.meta.displayName}</Button>
                </div>
            )
        case 'bootstrap':
        default:
            return (
                <div className={colClassName}>
                    <button className={"btn btn-default " + btnClassName}
                        onClick={(e) => handleClick(e, button)}
                    >{button.meta.displayName}</button>
                </div>
            )
    }
}

interface IProps {
    buttons: Array<IField>;
    form: any;
    theme: string;
    onSubmit: Function;
    onReset: Function;
}