import React, { Fragment, useEffect } from 'react';
import FormUtils from '../../utils/FormUtil';
import { IField, IFieldChange } from '../../constants/model-interfaces';
import FormContext from '../form-context';
import { IRenderField } from '../../constants/common-interface';
import { MSGS } from '../../constants';
export default abstract class BasicFormControl extends React.Component {
    static contextType = FormContext;
    displayType: string | undefined;
    field: IField;
    isFormControl: boolean;
    uuid: string;
    section: string;
    validation: {required: boolean|undefined, pattern: string|undefined};
    state: any;

    constructor(public props: IRenderField) {
        super(props);
        this.field = props;
        this.displayType = props.meta.displayType;
        this.section = props.section;
        this.isFormControl = FormUtils.isFormControl(props.meta);
        this.uuid = FormUtils.getUUID();
        this.validation = {
            required: props?.meta?.validation?.required?true:undefined,
            pattern: props?.meta?.validation?.pattern?props?.meta?.validation?.pattern:undefined
        }
        this.state = {
            error: {hasError: false, errorMsg: ''}
        }
    }
    render() {
        if (this.isFormControl) {
            return (<Display form={this.props.form}>
                {
                    this.control()
                }              
            </Display>)
        }
        return (<Fragment></Fragment>)
    }
    control() {
        switch (this.displayType) {
            case 'date':
                return (this.date())
            case 'select':
                return (this.select());
            case 'radio':
                return (this.radio());
            case 'checkbox':
                return (this.checkbox());
            case 'email':
                return (this.email());
            case 'number':
                return (this.number());
            case 'password':
                return (this.password());
            case 'text':
            case 'text_field':
            default:
                return (this.text());
        } 
    }
    abstract date(): JSX.Element;
    abstract text(): JSX.Element;
    abstract password(): JSX.Element;
    abstract email(): JSX.Element;
    abstract number(): JSX.Element;
    abstract radio(): JSX. Element;
    abstract checkbox(): JSX. Element;
    abstract select(): JSX.Element;
    abstract button(): JSX.Element;

    handleChange(e: any, val?: any) {
        const value = val !== undefined ? val : e.target.value;
        this.context.setField(this.section, this.field.name, value);
        const deps = this.context.getDependencies(this.section, this.field.name);
        deps && deps.forEach((dep: {section: string, type: string, value: string, field: string, url: string, currentValue: any, resetValue: any})=>{
            const type = dep.type;
            switch(type) {
                case 'exists': {
                    const depValue = dep.value;
                    const field = dep.field;
                    if (value != undefined) {
                        this.context.setFieldDisplay(dep.section, field, value === depValue)
                    }
                }
                break;
                case 'load': {
                    const field = dep.field;
                    const url = dep.url.replace('{0}', value);
                    loader(url).then(res => {
                        this.context.setFieldOptions(dep.section, field, res);
                        this.props.sync();
                    });
                }
                break;
                case 'enabled': {
                    const field = dep.field;
                    this.context.setFieldDisabled(dep.section, field, value !== dep.value);
                }
                break;
                case 'equals':
                    const field = dep.field;
                    if (value === dep.value) {
                        this.context.setField(dep.section, field, dep.currentValue);
                    } else if (dep.resetValue !== undefined) {
                        this.context.setField(dep.section, field, dep.resetValue);
                    }
                break;
            }
        });
        this.props.sync();
    }

    handleValidation() {
        const value = this.props.form.value;
        this.validate(value);
    }

    validate(value: string | undefined) {
        const meta = this.props.form;
        if (meta.validation?.required) {
            if (!value) {
                this.setState({error:{hasError: true, errorMsg: meta.validation?.required_detail?.errorMsg || MSGS.ERROR_MSG.REQUIRED}});
                return;
            }
        }
        if (meta.validation?.pattern) {
            const regx = new RegExp(meta.validation?.pattern);
            if (value && !regx.test(value)) {
                this.setState({error: {hasError: true, errorMsg: meta.validation?.pattern_detail?.errorMsg || MSGS.ERROR_MSG.PATTERN}});
                return;
            }
        }
        if (meta.validation?.min) {
            switch(meta.displayType) {
                case 'number':
                    if (value) {
                        const val = parseInt(value);
                        if (val < meta.validation.min) {
                            this.setState({error: {hasError: true, errorMsg: meta.validation?.min_detail?.errorMsg || MSGS.ERROR_MSG.MIN}});
                            return;
                        }
                    }
            }
        }
        if (meta.validation?.max) {
            switch(meta.displayType) {
                case 'number':
                    if (value) {
                        const val = parseInt(value);
                        if (val < meta.validation.max) {
                            this.setState({error: {hasError: true, errorMsg: meta.validation?.min_detail?.errorMsg || MSGS.ERROR_MSG.MAX}});
                            return;
                        }
                    }
            }
        }
        this.setState({error:{hasError: false, errorMsg: ''}});
    }
}

function loader(url: string) {
    return fetch(url).then(res => res.json());
}

function Display(props: any) {
    return (
        <Fragment>
            {props?.form?.display && props.children}
        </Fragment>
    )
}
