import React from 'react';
import BaseFormGroup from "./base-form-group";
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Sections from "./common/Sections";
import { Box } from '@mui/material';

export default class MuiFormGroup extends BaseFormGroup {
    tabs(): JSX.Element {
        const value = 0;
        return (
            <Box sx={{ maxWidth: { xs: 320, sm: 'unset', md: 'unset' }, overflowX: 'auto' }}>
                <Tabs variant="scrollable" 
                    allowScrollButtonsMobile={true} 
                    value={this.state.activeIndex} 
                    onChange={this.handleChange.bind(this)} aria-label="basic tabs example">
                    {
                        this.tabFields.map((tabField,index)=>{
                            const displayName = tabField?.meta?.displayName?tabField.meta.displayName:tabField.name;
                            return (
                                <Tab key={displayName} label={displayName}/>
                            )
                        })
                    }
                </Tabs>
            </Box>
        )
    }
    panels(): JSX.Element {
        return (<Sections sections={this.sectionFields} activeIndex={this.state.activeIndex}/>)
    }

    handleChange(arg: any, index: number) {
        this.setActiveIndex(index);
    }
    
}